function WorkText(props) {
    return (
      <section className="witsection">
      <div className="wit containersmall">
        <img src={props.image} alt="image"></img>
        <div className="mttgrid">
          <div className="sidebar">
            <h5>{props.sidebar}</h5>
          </div>
          <div className="main">
            <h3>{props.mainhead}</h3>
            <p>
              {props.main}
            </p>
          </div>
        </div>
      </div>
    </section>
  );
}

export default WorkText;



