function Profile(props){
    return (
      <div className="personwrapper">
        <a href="/Profilepage">
          {" "}
          <figure>
            <img src={props.image}></img>
            <figcaption>
              <h4>{props.profilename}</h4>
            </figcaption>
          </figure>
        </a>
      </div>
    );
}

export default Profile;