function Title(props){
    return (
      <section className="Title">
        <div className="page">
          <div className="h4pos">
            <div
              style={{
                width: "100%",
                height: "100%",
                position: "relative",
                paddingBottom: "20px",
                display: "flex",
              }}
            >
              <div
                style={{
                  position: "absolute",
                  writingMode: "vertical-rl",
                  top: "7%",
                  transform: "rotate(-180deg)",
                  fontSize: "1.111vw",
                  letterSpacing: "0.278vw",
                  color: "white",
                  fontWeight: "bold",
                }}
              >
                WE ARE MAG7
              </div>

              <h1 style={{ paddingLeft: "5.833vw" }}>
                {props.bighead1}
                <br />
                {props.bighead2}{" "}
              </h1>
            </div>
          </div>

          <div
            style={{
              background: "black",
              width: "100%",
              height: "100vh",
              zIndex: "-1",
              opacity: "60%",
            }}
          >
            <img
              style={{ width: "100%", height: "105vh", zIndex: "-1", objectFit:"cover"}}
              src={require("../images/space.gif")}
            ></img>
          </div>
          <div style={{background: "linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%)", width: "100%", height: "400px", position:"absolute", bottom:"-5vh"}}></div>
        </div>
      </section>
    );

}

export default Title;