import {ListGroup} from "reactstrap";
import React from "react";
import Cardo from "./Card";
import Toinf from "../images/Toinf.png"


function Cardslider() {
  return (
    <section style={{ height: "100%", paddingLeft: "2.5%" }}>
      <div
        style={{
          width: "60vw",
          height: "100%",
          position: "relative",
          paddingBottom: "20px",
          display: "flex",
        }}
      >
        <div
          style={{
            position: "absolute",
            writingMode: "vertical-rl",
            top: "7%",
            transform: "rotate(-180deg)",
            fontSize: "1.111vw",
            letterSpacing: "0.278vw",
            color: "white",
            fontWeight: "bold",
          }}
        >
          LATEST WORK
        </div>
        <h2 style={{ paddingLeft: "5.833vw" }}>WHAT WE'VE BEEN UP TO</h2>
      </div>
      <ListGroup horizontal style={{ display: "flex", overflowX: "auto" }}>
        <Cardo
          title="TO INFINITY AND BEYOND"
          text="Brent is a user experience design leader specializing in design strategy, architecture, and interface design. "
          source={require("../images/Toinf.png")}
          bbox="PBS / KCTS9"
        />
        <Cardo
          title="LIFE IS LIKE A BOX OF CHOCOLATES"
          text="Brent is a user experience design leader specializing in design strategy, architecture, and interface design.  "
          source={require("../images/beachchill.png")}
          bbox="AARP / MEMBER BENEFITS"
        />
        <Cardo
          title="WITH GREAT POWER COMES GREAT"
          text="Brent is a user experience design leader specializing in design strategy, architecture, and interface design. "
          source={require("../images/GREATCOMES.png")}
          bbox="RENOVATE AMERICA"
        />
        <Cardo
          title="TO INFINITY AND BEYOND"
          text="Brent is a user experience design leader specializing in design strategy, architecture, and interface design. "
          source={require("../images/beachchill.png")}
          bbox="pbs / kcts9"
        />
        <Cardo
          title="TO INFINITY AND BEYOND"
          text="Brent is a user experience design leader specializing in design strategy, architecture, and interface design. "
          source={require("../images/beachchill.png")}
          bbox="pbs / kcts9"
        />
        <Cardo
          title="TO INFINITY AND BEYOND"
          text="Brent is a user experience design leader specializing in design strategy, architecture, and interface design. "
          source={require("../images/beachchill.png")}
          bbox="pbs / kcts9"
        />
      </ListGroup>
    </section>
  );
}

export default Cardslider;
