function Title(props){
    return (
      <section className="Title">
        <div className="page">
          <div className="h4pos">
            <h4>{props.smallhead}</h4>
            <h1>
              {props.bighead1}
              <br />
              {props.bighead2}{" "}
            </h1>
          </div>
          <div
            style={{
              background: "black",
              width: "100%",
              height: "100vh",
              zIndex: "-1",
              opacity: "60%",
            }}
          >
            <img
              style={{
                width: "100%",
                height: "100vh",
                zIndex: "-1",
                objectFit: "cover",
              }}
              src={props.image}
            ></img>
          </div>
        </div>
      </section>
    );

}

export default Title;