import { Card } from "react-bootstrap";
import { CardBody, ListGroupItem } from "reactstrap";

function Cardo(props) {
  return (
    <ListGroupItem
      style={{
        background: "black",
        paddingRight: "4.444vw",
      }}
    >
      <Card
        style={{
          background: "black",
          width: "38.056vw",
          height: "100%",
          position: "relative",
          cursor: "url(./images/cursor.png) 60 24, auto"
        }}
      >
        <CardBody>
          <div
            style={{ width: "100%", height: "34.722vw", position: "relative" }}
          >
            {/* Card Img */}
            <img
              src={props.source}
              style={{
                width: "100%",
                maxHeight: "34.722vw",
              }}
            ></img>

            {/* Black Box */}
            <div
              style={{
                position: "absolute",
                bottom: "0",
                left: "0",
                padding: "1.389vw",
                background: "black",
                zIndex: "1",
              }}
            >
              <h5>{props.bbox}</h5>
            </div>
          </div>

          {/* Card Title */}
          <h3 style={{ paddingTop: "20px" }}>{props.title}</h3>
          <p style={{ paddingTop: "10px" }}>{props.text}</p>
        </CardBody>
      </Card>
    </ListGroupItem>
  );
}
export default Cardo;
