import Joinmag from "../components/Joinmag";
import Table from "../components/Table";


function Profilepage(){
    return (
      <>
        <section className="containersmall">
          <div
            className="mttgrid"
            style={{ gap: "4.444vw", marginTop: "17.917vw" }}
          >
            <img
              className="sidebar"
              src={require("../images/headshots/brent.png")}
            ></img>
            <div className="main">
              <h2>BRENT CANFIELD</h2>
              <h4>BRENT CANFIELD</h4>
              <p style={{ lineHeight: "2.500vw" }}>
                Brent is a user experience design leader specializing in design
                strategy, architecture, and interface design. His his UX design
                expertise is bolstered by a command of product management and
                development, making him able to generate big ideas and bring
                them to market. <br /> <br />
                He spent the first part of his career mastering how to design,
                and the second figuring out what should be designed and why.
                Because his unique set of experiences has taught him to go from
                left-brain to right-brain at the drop of a dime, he’s a
                visionary who can also deliver time-to-market rigor, and fiscal
                discipline.
              </p>
            </div>
          </div>
          <h2>LOREM IPSUM DOLOR SIT AMET</h2>
          <Table
            text1="BRENT CANFIELD"
            text2="MONTRI NUCHNANG"
            text3="BRENT CANFIELD"
            text4="BRENT CANFIELD"
            text5="BRENT CANFIELD"
            text6="BRENT CANFIELD"
            text7="BRENT CANFIELD"
            text8="BRENT CANFIELD"
          />
        </section>
        <Joinmag />
      </>
    );
}
export default Profilepage;