import React, { useEffect, useState } from "react";
import {
  Nav,
  NavLink,
  Bars,
  NavMenu,
  NavBtn,
  NavBtnLink,
} from "./Topbar2Elements";

const Topbar2 = () => {
  const [show, setShow] = useState(true);
  const [lastScrollY, setLastScrollY] = useState(0);

  const controlTopbar2 = () => {
    if (typeof window !== "undefined") {
      if (window.scrollY > lastScrollY && window.scrollY > 0) {
        // if scroll down hide the Topbar2
        setShow(false);
      } else {
        setShow(true);
      } 

      // remember current page location to use in the next move
      setLastScrollY(window.scrollY);
    }
  };

  useEffect(() => {
    if (typeof window !== "undefined") {
      window.addEventListener("scroll", controlTopbar2);

      // cleanup function
      return () => {
        window.removeEventListener("scroll", controlTopbar2);
      };
    }
  }, [lastScrollY]);

  const [color, setColor] = useState(false)
  const changeColor = () => {
    if (window.scrollY >= 90){
      setColor(true)
    } else {
      setColor(false)
    }
  }

window.addEventListener('scroll',changeColor)

  return (
    <nav className={`display ${show && "hidden"}`}>
      <>
        <div className={color ? "Navbg" : "Nav"}>
          <Nav>
            <NavLink to="/Home">
              <img src={require("../images/LOGO.png")} alt="logo"></img>
            </NavLink>
            <NavMenu>
              <NavLink to="/Work" activeStyle>
                WORK
              </NavLink>
              <NavLink to="/Method" activeStyle>
                METHOD
              </NavLink>
              <NavLink to="/Theteam" activeStyle>
                THE TEAM
              </NavLink>
              <NavLink to="/Letschat" activeStyle>
                LET'S CHAT
              </NavLink>
            </NavMenu>
          </Nav>
        </div>
      </>
    </nav>
  );
};

export default Topbar2;