import { useInView } from "react-intersection-observer";

function Methodtext() {
  const { ref: firstref, inView: firstrefvisible } = useInView({
    rootMargin: "-45% 15% -45%",
    triggerOnce: "true",
  });
  const { ref: secondref, inView: secondrefvisible } = useInView({
    rootMargin: "-45% 15% -45%",
    triggerOnce: "true",
  });
  const { ref: thirdref, inView: thirdrefvisible } = useInView({
    rootMargin: "-45% 15% -45%",
    triggerOnce: "true",
  });
  const { ref: fourthref, inView: fourthrefvisible } = useInView({
    rootMargin: "-45% 15% -45%",
    triggerOnce: "true",
  });
  const { ref: fifthref, inView: fifthrefvisible } = useInView({
    rootMargin: "-45% 15% -45%",
    triggerOnce: "true",
  });
  const { ref: sixthref, inView: sixthrefvisible } = useInView({
    rootMargin: "-45% 15% -45%",
    triggerOnce: "true",
  });
  const { ref: sevenref, inView: sevenrefvisible } = useInView({
    rootMargin: "-45% 15% -45%",
    triggerOnce: "true",
  });
  const { ref: eightref, inView: eightrefvisible } = useInView({
    rootMargin: "-45% 15% -45%",
    triggerOnce: "true",
  });

  return (
    <section className="hometext">
      <div
        className="containersmaller"
        style={{ display: "flex", flexDirection: "column" }}
      >
        <div className="methoddiv">
          <h3 ref={firstref}>
            We are a product design agency with deep expertise in &nbsp;
            <span
              className={`${""} ${
                firstrefvisible ? "highlightani highlight lblue " : ""
              }`}
            >
              user-centered design.
            </span>
          </h3>
          <p>
            We create useful, intuitive, and engaging digital experiences by
            developing a comprehensive understanding of your audience.
          </p>
        </div>
        <div className="methoddiv">
          <h3 ref={secondref}>
            We place a primacy on meaning, immediacy, and &nbsp;
            <span
              className={`${""} ${
                secondrefvisible ? "highlightani highlight green" : ""
              }`}
            >
              clarity.
            </span>
          </h3>
          <p>
            We work with proximity, visual hierarchy, grouping, typography,
            etc... pushing and pushing pixels to achieve radically optimized
            cognition. When the goal is to make an interface immediate and
            clear, it leads to its own type of beauty as a collateral effect.
          </p>
        </div>

        <div className="methoddiv">
          <h3 ref={thirdref}>
            We prefer the role of &nbsp;
            <span
              className={`${""} ${
                thirdrefvisible ? "highlightani highlight dblue" : ""
              }`}
            >
              partner
            </span>
            &nbsp; over vendor.
          </h3>
          <p>
            The work we do is very much pass/fail. Either we do an excellent
            job, or we’ve failed. Doing excellent work requires complete
            immersion in your business and mission. It is part of the mindset
            required to excel at product strategy and product design. To do UX
            properly, you’ve got to master the material.
          </p>
        </div>

        <div className="methoddiv">
          <h3 ref={fourthref}>
            Everyone puts &nbsp;
            <span
              className={`${""} ${
                fourthrefvisible ? "highlightani highlight red" : ""
              }`}
            >
              pen to paper.
            </span>
          </h3>
          <p>
            Every one of us, including the founders, puts pen to paper. At MAG7,
            we decided creating intellectual property at the highest levels was
            really important to us. Its how we stay vital and on the cutting
            edge.
          </p>
        </div>

        <div className="methoddiv">
          <h3 ref={fifthref}>
            <span
              className={`${""} ${
                fifthrefvisible ? "highlightani highlight lblue" : ""
              }`}
            >
              Trim the fat.
            </span>
          </h3>
          <p>
            All of our strategists and designers are fluent in project
            management fundamentals. We practice time to market rigor, fiscal
            discipline, and change management. This way you don’t have to pay a
            project manager and account manager to flank our strategists and
            designers. This creates efficiencies, ensuring your budget is spent
            on solving your problems and creating your IP.
          </p>
        </div>

        <div className="methoddiv">
          <h3 ref={sixthref}>
            <span
              className={`${""} ${
                sixthrefvisible ? "highlightani highlight green" : ""
              }`}
            >
              Stay Focused
            </span>
            &nbsp; and grow slowly to preserve world-class output.
          </h3>
          <p>
            We’ve picked one deep niche: Product Strategy and Product Design.
            All of our people are elite. We spend a lot of time cultivating our
            hires, because we need the best of the best. It is part of our value
            prop. We’re a premium brand that guarantees excellence.
          </p>
        </div>

        <div className="methoddiv">
          <h3 ref={sevenref}>
            <span
              className={`${""} ${
                sevenrefvisible ? "highlightani highlight dblue" : ""
              }`}
            >
              All-in
            </span>
            &nbsp; is the only way we know how to work
          </h3>
          <p>
            We love our work and we’re fun to work with. It’s what we were born
            to do. We’ve been told our enthusiasm is contagious. Let us off the
            chain and we will make a meaningful impact to your business.
          </p>
        </div>

        <div className="methoddiv">
          <h3 ref={eightref}>
            Let’s work &nbsp;
            <span
              className={`${""} ${
                eightrefvisible ? "highlightani highlight pink" : ""
              }`}
            >
              together!
            </span>
          </h3>
          <p>
            We’d love to hear more about how we can help you and to introduce
            you to our team and our work.
          </p>
        </div>
      </div>
    </section>
  );
}

export default Methodtext;
